
.container {
  max-width: 1240px;
  margin: 0 auto;
  padding: 25px 10px;
}
  .imageGrid {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 20px; /* Убираем внешние отступы для создания компактного макета */
    justify-content: center; /* Центрируем содержимое по горизонтали */
  align-items: center; 
  }
  
  
  .buttonLoadMore {
    background-color: purple;
    color: white;
    padding: 15px 25px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s;
    margin: 0 auto;
    display: block;
  }
  
  .buttonLoadMore:hover {
    background-color: #6a0dad;
  }
  
  .buttonLoadMore:focus {
    outline: none;
    border: 2px solid purple;
  }